* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  outline: 0;
}

body {
  background-size: contain;
  background-position: center center;
  background-attachment: fixed;
  background-repeat: no-repeat;
}

.container {
  display: flex;
  justify-content: right;
  width: 500px;
  height: 500px;
  border-color: black;
  box-sizing: 2px;
}